import React from 'react';
import { withStyles } from '@material-ui/core/styles';

import {
    AppBar,
    Paper,
    Toolbar,

    Link,
    Hidden,
    IconButton,
    Typography,
    List,
    ListItem,
    ListItemText,
    ListItemSecondaryAction,
    AccordionDetails,
    AccordionSummary,
    Accordion,
    LinearProgress,
    Container, Grid, MenuList, MenuItem, Button, DialogContent, CircularProgress, TextField
} from "@material-ui/core";
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { PayPalButton } from "react-paypal-button-v2";
import Util from './service/Util';
import Site from './service/Site';
import NewReleasesOutlinedIcon from "@material-ui/icons/NewReleasesOutlined";
import Cart from "./service/Cart";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import Radio from "@material-ui/core/Radio";


class RegisterCustomer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tab:0,
            termsAccept: false,
            total: 0,
            account: '',
            bal:0,
            baltotal:0
        }
    }

    makeid(length) {
        var result           = '';
        var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for ( var i = 0; i < length; i++ ) {
            result += characters.charAt(Math.floor(Math.random() *
                charactersLength));
        }
        return result;
    }

    componentWillUnmount() {
    }
    componentDidMount() {
        var url = new URL(window.location.href);
        var uid = url.searchParams.get("uid");
        var s = url.searchParams.get("s");
        var l = url.searchParams.get("l");
        this.setState({params:{uid:uid,s:s,l:l}})

        Site.instance.siteSubject.subscribe(()=>{
            setTimeout(()=>{
                this.showPaypal()
            }, 2000)
        })
    }

    myTxId=''
    showPaypal(){
        if (!window.paypal) {
            Site.instance.showToast("Paypal not ready.", "error", "top");
            return;
        }
        this.myTxId = this.makeid(7)
        var url = new URL(window.location.href);
        var p = url.searchParams.get("p");

        if (!p){
            //Site.instance.showToast("Error! Contact RamtTD!")
            p = Site.instance.site.registration_pp_plan_id
        }else{
        }
        console.log(p)
        this.setState({planid: p})
        window.paypal.Buttons(
            {
                onInit1: function(data, actions) {
                    actions.disable()
                    //alert(5)

                },
                onClick: (data, actions)=> {
                    let err = ''
                    if (!err && this.fullName.value.length<5) err = "Enter your full name"
                    if (!err && !/^0[1-9][0-9]{9}$/.test(this.mobile.value)) err = "Enter your correct mobile no"
                    if (!err && !/^0[1-9][0-9]{9}$/.test(this.phone.value)) err = "Enter your correct phone no"
                    if (!err && this.shopName.value.length<5) err = "Enter your business name"
                    if (!err && this.address.value.length<10) err = "Enter your business address"
                    if (!err && !Util.instance.validateEmail(this.email.value)) err = "Enter your email address"
                    if (!err && !this.termsAccept.checked) err = "You have to read and accept terms and conditions "
                    if (err!==''){
                        Site.instance.showToast(err, "error", "top" )
                        return actions.reject();
                    }else {
                        //actions.enable()
                        Site.instance.sendEventLog({data: JSON.stringify({txid:this.myTxId,email:this.email.value,mobile:this.mobile.value,shopName:this.shopName.value}), event: "reg_click"});
                        return actions.resolve();
                    }
                },
                createSubscription: (data, actions) => {
                    Site.instance.sendEventLog({data: JSON.stringify({txid:this.myTxId,email:this.email.value,mobile:this.mobile.value,shopName:this.shopName.value}), event: "reg_csubs"});
                    return actions.subscription.create({
                        /* Creates the subscription */
                        plan_id: p
                    });
                },
                onApprove: (data, actions) => {

                    Site.instance.sendEventLog({data: JSON.stringify({txid:this.myTxId,email:this.email.value,mobile:this.mobile.value,shopName:this.shopName.value}), event: "reg_approve"});
                    Site.instance.showWaiting(true, "Please wait ...");
                    console.log(data)
                    Site.instance.completeOrder(
                        {
                            fullName: this.fullName.value,
                            mobile: this.mobile.value,
                            phone: this.phone.value,
                            shopName: this.shopName.value,
                            address: this.address.value,
                            email: this.email.value,
                            paydetails: JSON.stringify(data)}, (orderid, message) => {
                            Site.instance.showWaiting(false, "");
                            if (orderid === 0) {
                                Site.instance.sendEventLog({data: JSON.stringify({txid:this.myTxId,message:message,email:this.email.value,mobile:this.mobile.value,shopName:this.shopName.value,data:data}), event: "reg_appr_err"})
//                                Site.instance.sendEventLog({data: message, event: "onapproveerror"})
                                Site.instance.showToast(message, "error", "bottom");
                            } else {
                                Site.instance.showMessageBox("Thank you for registration", "Success", "top");
                                Site.instance.sendEventLog({data: JSON.stringify({txid:this.myTxId,message:message,email:this.email.value,mobile:this.mobile.value,shopName:this.shopName.value,data:data}), event: "reg_appr_ok"})
//                                Site.instance.sendEventLog({data: message, event: "onapproveok"})
                                this.setState({alertOpen: true, alertMessage: message, alertOrderNo: orderid})
                            }
                        });

                },
                onError:(err)=>{
                    let data = {
                        err: err && err.message? err.message : err,
                        stacktrace: Util.instance.getStackTrace()
                    }
//                    Site.instance.sendEventLog({data: JSON.stringify(data), event: "pperror"})
                    Site.instance.sendEventLog({data: JSON.stringify({txid:this.myTxId,email:this.email.value,mobile:this.mobile.value,shopName:this.shopName.value,data:data}), event: "reg_exception"})
                    Site.instance.showToast("Payment error. Try another browser or payment method", "error", "bottom")
                }
            }).render('#paypalid1');
    }
    buy(p){
        var product=''
        if (p==1) {
            product = this.state.product
        }
        if (p==2) {
            if (this.state.baltype == 'c') product = this.state.baltotal + 'b'
            if (this.state.baltype !== 'c') product = this.state.baltype+'b'
        }
        let devid=Site.instance.getQueryVariable("devid")
        let s=Site.instance.getQueryVariable("s")
        Site.instance.showWaiting(true, "Wait...")
       Site.instance.callApi("order", {op:'create',product:product,devid:devid,uid:this.state.params.uid, s:s}).then((d)=>{

           if (d&&d.url){
               window.location.href=d.url
           }
       }).finally(()=>{
           Site.instance.showWaiting(false)
       })
    }
    calcBalTotal(){
        let b = 0
        if (this.state.baltype && this.state.baltype!=='c') b=this.state.baltype*1
        if (this.state.baltype && this.state.baltype=='c') b=this.state.camount*1
        if (isNaN(b)) b=0
        this.setState({baltotal:b})
    }
    render() {
        const { classes } = this.props;
        return (
            <Grid container spacing={3} style={{minHeight:"100%", marginTop:50, marginBottom:50}} className="gridNoMargin background1"   justify="center"
            >
                <Grid item xs={12} sm={6} md={6} style={{ zIndex:10}} >
                    <Paper style={{ padding: 10, paddingTop:30, paddingBottom: 30, textAlign: "left", minHeight: 120, position:"relative",
                        backgroundColor:"#FFF"
                    }} elevation={3}>
                        <div style={{textAlign: "center"}}>
                        <img src="./logo.png" style={{  marginBottom: 20,maxWidth:200}}/>
                            <Typography variant="body2" style={{marginBottom: 10, color: '#888'}}>
                                UID: {this.state.params && this.state.params.uid ? this.state.params.uid : ""} &nbsp;&nbsp;â&nbsp;&nbsp; Balance: $123.00 &nbsp;&nbsp;â&nbsp;&nbsp; Expires: 3/3/2023
                            </Typography>
                            <Tabs   variant="fullWidth" value={this.state.tab} onChange={(e,i)=>{
                                console.log(i)
                                this.setState({tab:i})
                            }} aria-label="disabled tabs example" centered>
                                <Tab label="Buy Account"/>
                                <Tab label="Topup"/>
                                <Tab label="Buy for Friend"/>
                            </Tabs>


                        </div>
                        {this.state.tab == 0 && <Grid item xs={12} sm={12} md={12} style={{marginTop:20}}>

                            <FormLabel id="demo-row-radio-buttons-group-label" ></FormLabel>
                            <RadioGroup
                                row
                                aria-labelledby="demo-row-radio-buttons-group-label"
                                name="row-radio-buttons-group"
                                onChange={(e,i)=>{
                                    var t=0;
                                    if (i=='3m') t=12;
                                    if (i=='6m') t=22;
                                    if (i=='1y') t=40;
                                    this.setState({total: t, bal:0, product:i})
                                }}
                            >
                                <FormControlLabel value="3m" control={<Radio />} label="3 Months" />
                                <FormControlLabel value="6m" control={<Radio />} label="6 Months" />
                                <FormControlLabel value="1y" control={<Radio />} label="1 Year" />
                            </RadioGroup>


                            <TextField
                                required
                                size="small"
                                margin="dense"
                                label="Email (to get payment receipt)"
                                type="email"
                                fullWidth
                                inputRef={e => this.email = e}
                            />


                            <div style={{textAlign: "center", marginTop:30}}>
                                <Typography variant="body2" style={{display: 'flex',alignItems: 'center', justifyContent:"center", fontSize: "large", color:"black"}}>
                                    Total: ${this.state.total}
                                </Typography>

                                <Button variant="contained" color="primary" style={{marginTop:20}} value={"Buy with Crypto"}
                                        onClick={()=>this.buy(1)}
                                        disabled={this.state.total<=0}>Pay with Crypto</Button>
                            </div>
                        </Grid>
                        }


                        {this.state.tab ==1  && <Grid item xs={12} sm={12} md={12} style={{padding:20}}>
                            <Typography style={{ marginBottom: 15, marginTop: 5, color: "grey"}} variant="body2">
                                Top up your balance (bulk buy, buy for someone else). Up to 20% off!
                            </Typography>


                            <FormControl fullWidth style={{marginTop: 10}}>
                                <InputLabel>Amount</InputLabel>
                                <Select
                                    labelId="demo-simple-select-label"
                                    id="demo-simple-select"
                                    inputRef={e => this.balType = e}
                                    label="BalanceType"
                                    onChange={(e,v)=>{
                                        this.setState({baltype:e.target.value}, ()=>{
                                            this.calcBalTotal()
                                        })
                                    }}
                                >
                                    <MenuItem value={"20"}>$20</MenuItem>
                                    <MenuItem value={"50"}>$50 (6% off, $53 credit)</MenuItem>
                                    <MenuItem value={"100"}>$100 (10% off, $110 credit)</MenuItem>
                                    <MenuItem value={"200"}>$200 (20% off, $240 credit)</MenuItem>
                                    <MenuItem value={"c"}>Custom Amount</MenuItem>
                                </Select>
                            </FormControl>
                            {this.state.baltype&& this.state.baltype=='c' && <TextField
                                required
                                size="small"
                                margin="dense"
                                label="Custom Amount"
                                type="currency"
                                fullWidth
                                inputRef={e => this.camount = e}
                                onChange={(e)=>{
                                    this.setState({camount:e.target.value},()=>{
                                        this.calcBalTotal()
                                    })}}
                            />}

                            <TextField
                                required
                                size="small"
                                margin="dense"
                                label="Email (to send payment receipt)"
                                type="email"
                                fullWidth
                                inputRef={e => this.email = e}
                            />
                            <div style={{textAlign: "center", marginTop:30}}>
                                <Typography variant="body2" style={{display: 'flex',alignItems: 'center', justifyContent:"center", fontSize: "large", color:"black"}}>
                                    Total: ${this.state.baltotal}
                                </Typography>
                                <Button variant="contained" color="primary" value={"Buy with Crypto"}
                                        style={{marginTop:30}}
                                        onClick={()=>this.buy(2)}
                                disabled={this.state.baltotal<=0}>Pay with Crypto</Button>
                            </div>

                        </Grid>
                        }


                        <div id="paypalid1"></div>

                        <Typography style={{fontSize: "8px", color: "#888"}}>
                            {!this.state.planid? '' : this.state.planid}
                        </Typography>
                    </Paper>

                </Grid>
    </Grid>

    );
    }
}

export default RegisterCustomer;
